import s from './Post.module.css'

type PropsType = {
    message: string
    likesCounter: number
}

const Post: React.FC<PropsType> = (props) => {
    return (
        <div className={s.item}>
            <img src="data:image/png;base64," />
            {props.message}
            <div className={s.like}>
                like: {props.likesCounter}
            </div>
        </div>
    );
}

export default Post;